@import "../_activity-variables.less";
@import "../_activity-mixins.less";

@keyframes fn-activity-red-packet-rain-spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}

.fn-@{activity-ns}-red-packet-rain{
	.fixed(0,0,_,0);
	.size(100%);
	margin: 0 auto;
	padding-top: 1.8rem;

	.loading{
		.absolute(50%,_,_,50%);
		margin: -1.4rem 0 0 -1.4rem;
		.size(2.8rem);
		background: data-uri('@{activity-image-path}/red-packet-rain-bg.png') no-repeat 0 0;
		background-size: 100% 100%;

		&:before{
			content: '';
			display: block;
			.size(100%);
			background: data-uri('@{activity-image-path}/red-packet-rain-loading.png') no-repeat 0 0;
			background-size: 100% 100%;
			animation: fn-activity-red-packet-rain-spin 1s infinite linear;
		}
	}

	&.full-screen{
		padding-top: 0rem;
	}

	.layer{
		.absolute(0,_,_,0);
		.size(100%);
		background-repeat: no-repeat;
	}

	.wrap{
		.size(100%);
		position: relative;
		background-position: center bottom;
		background-size: 100% auto;
	}

	.enter-tip{
		background-size: 100% auto;
		background-position: center bottom;
	}

	.canvas{
		.countdown{
			.absolute(0.4rem,0.4rem,_,_);
			background: rgba(0,0,0,.5);
			color: #fff;
			line-height: 1.6;
			padding: 0 0.64rem;
			border-radius: 0.64rem;
		}
	}

	.ends-tip{
		background-size: 100% auto;
		background-color: rgba(0,0,0,.6);

		.tip{
			.absolute(50%,_,_,0);
			width: 100%;
			text-align: center;
			color: #fff;

			.title{
				margin-bottom: 0.16rem;
				font-size: 0.64rem;
			}
		}

		.btn{
			.absolute(_,_,16%,50%);
			transform: translateX(-50%);
			width: 50%;
			background: #f1ec51;
			line-height: 2;
			border-radius: 0.16rem;
			text-align: center;
			color: #c62239;
		}
	}

	.dialog-layer{
		background-size: 100% auto;
		background-position: center center;
		background-color: rgba(0,0,0,0.6);

		.dialog{
			.absolute(50%,_,_,50%);
			transform: translate(-50%, -50%);
			background-position: center center;
			background-size: 100% 100%;
			width: 56%;
			max-width: 14rem;

			&:before{
				display: block;
				content: '';
				padding-top: 125.73%;
			}

			.close-btn{
				.absolute(-1.6rem, -1.6rem,_,_);
				color: #fff;
				border: 1px solid #fff;
				border-radius: 50%;
				.size(1.04rem);
				text-align: center;
				line-height: 1.04rem;
			}

			.content{
				.absolute(0,0,_,_);
				.size(100%);
				text-align: center;
				color: #fff;

				.text{
					padding-top: 54%;

					.ht{
						color: #F1EC51;
					}

					.tip{
						font-size: 0.64rem;
					}
				}

				.sub{
					margin-top: -0.2rem;
					padding-top: 1%;

					.num{
						font-size: 280%;
					}
				}

				.btn-list{
					.absolute(_,_,6%,0);
					width: 100%;

					.btn{
						&.pl{
							padding-left: 10%;
						}

						&.pls{
							padding-left: 5%;
						}

						&.pr{
							padding-right: 10%;
						}

						&.prs{
							padding-right: 5%;
						}

						> a{
							border-radius: 0.16rem;
							display: block;
							color: #DF0915;
							background: #F1EC51;
							line-height: 1.8;
						}

					}
				}
			}
		}
	}
}
